@mixin ui-disabled(){
    // disabled  ( )
    &.disabled{
        opacity: 0.5;
        pointer-events: none;
        position: relative;
        z-index: -1;
        // disabled  ie10 
        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            /* IE10+ CSS styles go here */
            &:after{
                content:'';
                position: absolute;
                width:100%;
                height:100%;
                top:0;left:0;
                background:#ffffff;
                opacity: 0;
                z-index: 1;
            }
        }
    }
}


