// @include em-calc(width, 80%, '-', 1px);
// @include em-calc(width, 1vw, '-', 1px);
// ******************** code comments ********************
@mixin em-calc($property, $num, $sign, $calc-px){
    #{$property}: calc(#{$num}, #{$sign}, #{$calc-px});
    $calc-em: em($calc-px);
    #{$property}: calc(#{$num}, #{$sign}, #{$calc-em});
}


